import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {Link as RouterLink} from "react-router-dom";
import Web3 from "web3";
import lottery2_abi from "../abis/Lottery2.json";

import {
    Avatar,
    Button,
    Container,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    ToggleButton,
    ToggleButtonGroup,
    Typography
} from "@mui/material";

import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";

import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import CurrencyBitcoinIcon from '@mui/icons-material/CurrencyBitcoin';

const LOTTERY2_CONTRACT_FUSE1 = '0x4C452c2E6f27942AEab5e0c5158792b59e52814e';
const LOTTERY2_CONTRACT_AVAX1 = '0xd5a5A1628f03Df66ef5fcc68e17dfDEf6f07CdA3';
const LOTTERY2_CONTRACT_BNB1 = '0xB830d3e0f85f47B7A618a8a59e0564b4f28A2DF8';



export default function Lottery() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [displayUSD, setDisplayUSD] = useState(false);

    const [lottery2FUSE, setLottery2FUSE] = useState({});
    const [lottery2AVAX, setLottery2AVAX] = useState({});
    const [lottery2BNB, setLottery2BNB] = useState({});

    const getLottery2Info = () => {
        if (!wallet.address) return;

        getLottery2GameDetails(LOTTERY2_CONTRACT_FUSE1, setLottery2FUSE, rpc.fuse);
        getLottery2GameDetails(LOTTERY2_CONTRACT_AVAX1, setLottery2AVAX, rpc.avax);
        getLottery2GameDetails(LOTTERY2_CONTRACT_BNB1, setLottery2BNB, rpc.bnb);
    }

    const getLottery2GameDetails = (contract_address, callback, _rpc) => {

        const web3 = new Web3(_rpc);

        const lottery = new web3.eth.Contract(lottery2_abi, contract_address);

        const calls = [
            lottery.methods.ticketCost().call,
            lottery.methods.adminFee().call,
            lottery.methods.lotteryID().call,
            lottery.methods.entriesAllowed().call,
            lottery.methods.nextDraw().call,
            lottery.methods.nextDrawInterval().call,
            lottery.methods.totalFees().call,
            lottery.methods.totalWinnings().call,
            web3.eth.getBalance(contract_address)
        ];

        makeBatchRequest(web3, calls).then(resp => {
            const [
                ticketCost,
                adminFee,
                lotteryID,
                entriesAllowed,
                nextDraw,
                nextDrawInterval,
                totalFees,
                totalWinnings,
                jackpot
            ] = resp;

            callback({
                ticketCost: ticketCost / (10 ** 18),
                adminFee,
                lotteryID,
                entriesAllowed,
                nextDraw: nextDraw * 1000,
                nextDrawInterval,
                totalFees,
                totalWinnings,
                jackpot: jackpot * (1 - adminFee / 100)
            });
        })

    }

    useEffect(() => {
        getLottery2Info();
        const interval = setInterval(getLottery2Info, 10000)
        return () => clearInterval(interval);
    }, [rpc.fuse, wallet.address]);

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12} style={{textAlign: 'right'}}>
                    <ToggleButtonGroup
                        value={displayUSD}
                        exclusive
                        onChange={()=>setDisplayUSD(!displayUSD)}
                        aria-label="text alignment"
                        color="primary"
                    >
                        <ToggleButton value={false}>
                            <CurrencyBitcoinIcon />
                        </ToggleButton>
                        <ToggleButton value={true}>
                            <AttachMoneyIcon />
                        </ToggleButton>
                    </ToggleButtonGroup>
                </Grid>

                <LotteryGameInfo
                    contract_address={LOTTERY2_CONTRACT_FUSE1}
                    id={lottery2FUSE.lotteryID}
                    cost={lottery2FUSE.ticketCost}
                    jackpot={lottery2FUSE.jackpot}
                    nextDraw={lottery2FUSE.nextDraw}
                    entriesAllowed={lottery2FUSE.entriesAllowed}
                    currency="FUSE"
                    basePrice={wallet.prices.fuse.native?.inUSD || 0}
                    displayUSD={displayUSD}
                />

                <LotteryGameInfo
                    contract_address={LOTTERY2_CONTRACT_AVAX1}
                    id={lottery2AVAX.lotteryID}
                    cost={lottery2AVAX.ticketCost}
                    jackpot={lottery2AVAX.jackpot}
                    nextDraw={lottery2AVAX.nextDraw}
                    entriesAllowed={lottery2AVAX.entriesAllowed}
                    currency="AVAX"
                    basePrice={wallet.prices.avax.native?.inUSD || 0}
                    displayUSD={displayUSD}
                    customColor={lottery2AVAX.lotteryID == 2 && "#E76223"}
                    customListItem={lottery2AVAX.lotteryID == 2 && <ListItem>
                        <ListItemAvatar>
                            <Avatar src="https://coineus.app/assets/tokens/avax/0x51e48670098173025C477D9AA3f0efF7BF9f7812/logo.png"/>
                        </ListItemAvatar>
                        <ListItemText primary={displayUSD ? `~${CoineusUSDFormat(100 * wallet.prices.avax['0x51e48670098173025C477D9AA3f0efF7BF9f7812'].inUSD)}` : `${CoineusCryptoFormat(100)} DGNX`}
                                      primaryTypographyProps={{
                                          style: {fontWeight: 700},
                                          variant: 'h5',
                                          color: '#E76223'
                                      }}
                                      secondary={`Bonus Token`}
                        />
                    </ListItem>}
                />

                <LotteryGameInfo
                    contract_address={LOTTERY2_CONTRACT_BNB1}
                    id={lottery2BNB.lotteryID}
                    cost={lottery2BNB.ticketCost}
                    jackpot={lottery2BNB.jackpot}
                    nextDraw={lottery2BNB.nextDraw}
                    entriesAllowed={lottery2BNB.entriesAllowed}
                    currency="BNB"
                    basePrice={wallet.prices.bnb.native?.inUSD || 0}
                    displayUSD={displayUSD}
                />

            </Grid>
        </Container>
    );
}

function LotteryGameInfo(props) {

    const {contract_address, id, cost, jackpot, currency, nextDraw, entriesAllowed, basePrice, displayUSD, customColor, customListItem} = props;

    const [drawingTime, setDrawingTime] = useState(undefined);

    useEffect(() => {
        setDrawingTime(msToHMS(timeToNextDraw(nextDraw)));
        const interval1 = setInterval(() => {
            setDrawingTime(msToHMS(timeToNextDraw(nextDraw)));
        }, 1000);
        return () => clearInterval(interval1);
    },[nextDraw]);

    return <Grid item xs={12}>
        <Paper>
            {
                (timeToNextDraw(nextDraw) > 0) ? <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Drawing
                    in {drawingTime}</Typography> : <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Waiting for Winner</Typography>
            }
            <Typography variant="caption" color="primary"
                        style={{float: "right", padding: '15px 15px 0 15px'}}>Max Entries: {entriesAllowed} - Draw# {id}</Typography>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar src={`https://coineus.app/assets/tokens/${currency.toLowerCase()}/logo.png`}/>
                    </ListItemAvatar>
                    <ListItemText primary={displayUSD ? `~${CoineusUSDFormat(cost * basePrice)}` : `${CoineusCryptoFormat(cost)} ${currency}`}
                                  primaryTypographyProps={{
                                      style: {fontWeight: 700},
                                      variant: 'h5'
                                  }}
                                  secondary={`Ticket Price`}/>
                    <ListItemText primary={displayUSD ? `~${CoineusUSDFormat(jackpot / (10**18) * basePrice)}` : `${CoineusCryptoFormat(jackpot / (10**18))} ${currency}`}
                                  primaryTypographyProps={{
                                      style: {fontWeight: 700},
                                      variant: 'h5',
                                      textAlign: 'right'
                                  }}
                                  secondary={`Current Jackpot`}
                                  secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                {customListItem}
                <ListItem>
                    <RouterLink to={`/lottery/${currency.toLowerCase()}/${contract_address}`}
                                style={{width: '100%', textDecoration: 'none'}}>
                        <Button
                            fullWidth
                            variant="contained"
                            style={customColor ? {backgroundColor: customColor} : {}}
                        >Enter</Button>
                    </RouterLink>
                </ListItem>
            </List>
        </Paper>
    </Grid>
}

function timeToNextDraw(d) {
    return d - new Date();
}

function msToHMS(ms) {
    let h = ms / 3.6e6 | 0;
    let m = (ms % 3.6e6) / 6e4 | 0;
    let s = (ms % 6e4) / 1e3 | 0;
    return `${h}:${('' + m).padStart(2, '0')}:${('' + s).padStart(2, '0')}`;
}